import { CqsItem } from "@rp/common/api-types"

const getTextFromKeys = (item: CqsItem, keys: Array<keyof CqsItem>) => {
  let text = ""

  for (const key of keys) {
    const value = item[key]
    if (typeof value === "string") {
      text = text.concat(value)
      text = text.concat(" - ")
    }
  }

  return text.slice(0, -3)
}

const getWatchlistItemDescription = (item: CqsItem) => {
  switch (item.category) {
    case "Companies":
      return getTextFromKeys(item, ["group2", "group3", "group1"])

    case "People":
      return getTextFromKeys(item, ["group1", "group2"])

    case "Topics":
      return getTextFromKeys(item, ["description"])

    case "Concepts":
      return getTextFromKeys(item, ["group1"])

    case "Places":
      return getTextFromKeys(item, ["group4", "group2"])

    case "Organizations":
      return getTextFromKeys(item, ["group2", "group1"])

    case "Products":
      return getTextFromKeys(item, ["group1", "group2"])
    default:
      return ""
  }
}

export { getWatchlistItemDescription }
